import * as React from "react"
import { graphql } from "gatsby"

import Layout from "../components/layout"
import Seo from "../components/seo"
import ImageBackground from "../images/images_homenowords-03.jpg"
import { makeStyles } from "@material-ui/core"
import FlickerEffect from "../components/flickerEffect"
const useStyles = makeStyles(() => ({
   image: {
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      '& div': {
         backgroundImage: `url(${ImageBackground})`,
         backgroundSize: '100% auto',
         backgroundRepeat: 'no-repeat',
         backgroundPosition: 'center',
         display: 'flex',
         justifyContent: 'center',
         alignItems: 'center',
         position:'relative',
         "@media (max-width: 767px)": {
            height: '25vh',
            width: '90vw',
         },
         "@media (min-width: 768px) and (max-width: 1023px)": {
            height: '33vh',
            width: '90vw',
         },
         "@media (min-width: 1024px) and (max-width: 1280px)": {
            height: '35vw',
            width: '70vw',
         },
         "@media (min-width: 1281px) and (max-width: 1439px)": {
            height: '50vh',
            width: '50vw',
         },
         "@media (min-width: 1440px)": {
            height: '48vh',
            width: '45vw'
         },
      }
   },
   configArtical: {
      textAlign: 'justify',
      marginBottom: 10
   },
   dynamicContent: {
      position: 'absolute',
      borderTop: '1px solid white',
      borderBottom: '1px solid white',
      textTransform: 'uppercase',
      color: "white",
      padding: '5px 0',
      textAlign: 'center',
      fontSize: '1rem',
      "@media (max-width: 767px)": {
         width: '50vw',
         top: '47%',
      },
      "@media (min-width: 768px) and (max-width: 1023px)": {
         width: '40vw',
         top: '52%',
      },
      "@media (min-width: 1024px) and (max-width: 1280px)": {
         width: '25vw',
         top: '50%',
      },
      "@media (min-width: 1281px) and (max-width: 1439px)": {
         width: '20vw',
         top: '54%',
      },
      "@media (min-width: 1440px)": {
         width: '20vw',
         top: '54%',
      },
   },  
   titleH4: {
      textAlign: 'left',
      fontSize: '1.7rem',
      color: 'black',
      margin: '20px 0 10px',
      fontWeight: '400'
   }
}));

const listWordPhilosophy = ['LIFE', 'SELF', 'WAY'];

const Philosophy = ({ data, location }) => {
   const classes = useStyles()
   const siteTitle = data.site.siteMetadata?.title || `Title`

   return (
      <Layout location={location} title={siteTitle}>
         <Seo title="Pholisophy" description="Make Technology Work for Human Beings, Not The Other Way Around"/>
         <div>
            <div className={classes.image}>
               <div>
                  <p className={classes.dynamicContent}>
                     THE BALANCED <FlickerEffect listWord={listWordPhilosophy}></FlickerEffect>
                  </p>
               </div>
            </div>
            <article>
               <h4 className={classes.titleH4}>Our Philosophy: Make Technology Work for Human Beings, Not The Other Way Around.</h4>
               <p className={classes.configArtical}>We were promised a simpler life through technology. Having portable computers and gadgets and constant access to information
               was supposed to give us all the things we want instantaneously. It was promised that this would improve our lives.
               Instead, now we have more things to carry and keep track of, and we are busier because our bosses and customers expect instant
               replies 24-7. Even if this isn't your story, you are probably like many of us who are addicted to constantly interacting with our
                  phones and gadgets and checking social networks.</p>

               <p className={classes.configArtical}>Our goal is simple: to enable a return to a more balanced life by taming technology to work for us. The software we build,
               the websites we develop, and the projects we work on are all opportunities for us to simplify our relationships with technology in
                     the way our users use them and also in the way build them.</p>

               <p className={classes.configArtical}>We commit to creating products and providing services that enhance our relationships and help keep our commitments to our clients,
                     our community, and ourselves.</p>
            </article>
         </div>
      </Layout>
   )
}

export default Philosophy

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      nodes {
        excerpt
        fields {
          slug
        }
        frontmatter {
          date(formatString: "MMMM DD, YYYY")
          title
          description
        }
      }
    }
  }
`
